<template>
    <v-container 
        class="d-flex flex-column justify-start align-center mt-10 b-g rounded-lg">
        
        <h3 class="t-t-color text-h4 my-4 font-weight-medium"> Task </h3>

        <v-container fluid rounded label="Task" class="d-flex flex-row">
            <v-col cols="16" class="mx-2 pa-0 ma-0">
                <v-textarea text-narrow
                    id="task-text" class="text-caption"
                    v-model="text" solo no-resize rows="20">
                </v-textarea>
            <div>
                <v-btn class="mx-1" @click="parseText">Parse</v-btn>
                <v-btn class="mx-1" @click="previewTask">Preview</v-btn>
                <v-btn class="mx-1" @click="saveText">Save</v-btn>
                <v-btn class="mx-1" @click="restoreText">Restore</v-btn>
            </div>

            <v-overlay :value="taskOverlay" >
                    <v-sheet 
                        elevation="4" width="600" 
                        class="pa-4 ma-2 d-flex flex-column justify-flex-start align-flex-start rounded-lg"
                    >

                    <h3>Task text: {{ task_text }}</h3>
                    <div class="d-flex flex-column ma-2 pa-1" v-for="(q, i) in questions" :key="q">
                        <h4 class="ma-1"> Question: {{ q.text }} </h4>
                        <div class="ma-1 pa-1" v-if="q.text.includes(answers[i].name)">
                            <p v-for="(item, index) in answers[i].correct_values" :key="index" >{{item}}</p>
                            <p v-for="(item, index) in answers[i].incorrect_values" :key="index" >{{item}}</p>
                        </div>
                    </div>

                    <v-btn @click="taskOverlay = false">Close</v-btn>
                    </v-sheet>
            </v-overlay>

            </v-col>
            <v-col cols="16" class="mx-2">
                <h4> Variables </h4>
                        <v-chip
                            v-for="(variable, i) in variables" :key="i"
                            @click="variableInfo(i)"
                            :elevation="6" color="primary"
                            class="mx-2 my-1 px-6 py-4 pointer">
                            {{ variable.name.substr(1) }}
                        </v-chip>
                        <v-overlay 
                            :value="overlayV" v-if="variables.length != 0">
                            <v-sheet 
                                elevation="4" width="250" 
                                class="pa-4 ma-2 d-flex flex-column justify-flex-start align-flex-start rounded-lg"
                            >
                                <h4 class="ma-2"> {{ variables[indexV].name }} </h4>
                                <p class="ma-2"> Definition: {{ variables[indexV].definition }} </p>
                                <p class="ma-2"> Range: {{ variables[indexV].range }} </p>
                                <p class="ma-2"> Value: {{ variables[indexV].value }} </p>
                                <v-btn @click="overlayV = false">Close</v-btn>
                            </v-sheet>
                        </v-overlay>

                <h4>Questions</h4>
                <v-sheet 
                    v-for="question in questions" :key="question" 
                    class="d-flex flex-row align-center ma-1 pa-1"
                    width="50%"
                    elevation="3"
                    rounded
                >
                    <v-tooltip top >
                        <template #activator="{on}">
                            <v-chip v-on="on" label="text" class="mx-1"> {{ question.text }} </v-chip>
                        </template>
                        <span>Text</span>
                    </v-tooltip>
                </v-sheet>
                <h5>Answers</h5>
                    <v-chip
                        v-for="(answer, i) in answers" :key="i"
                        @click="answerInfo(i)"
                        :elevation="6" color="primary"
                        class="mx-2 my-1 px-6 py-4 pointer">
                        {{ answer.name.substr(1) }}
                    </v-chip>
                    <v-overlay 
                        :value="overlayA" v-if="answers.length != 0">
                        <v-sheet 
                            elevation="4" width="250" 
                            class="pa-4 ma-2 d-flex flex-column justify-flex-start align-flex-start rounded-lg"
                        >
                            <h4 class="ma-2"> {{ answers[indexA].name }} </h4>
                            <p class="ma-2" v-for="correct in answers[indexA].correct" :key="correct"> 
                                Correct: {{ correct }} 
                            </p>
                            <p class="ma-2" v-for="incorrect in answers[indexA].incorrect" :key="incorrect"> 
                                Incorrect: {{ incorrect }} 
                            </p>
                            <v-btn @click="overlayA = false">Close</v-btn>
                        </v-sheet>
                    </v-overlay>
            </v-col>
        </v-container>

    </v-container>
</template>

<script>
    import { tokenize } from "../myfiles/tokenizer"
    import { parseMathMarkup, taskQuestion, variableInfo } from "../myfiles/taskHandler"
    import { randomInt, evaluate } from "mathjs";

    export default {
        data() {
            return {
                text: "",
                task_text: "",
                variables: [],
                questions: [],
                answers: [],

                overlayV: false,
                overlayA: false,
                taskOverlay: false,
                indexV: 0,
                indexA: 0,
            };
        },
        computed: {
            getText() { return this.text; }
        },

        created() {
            window.addEventListener('beforeunload', function(event) {
                event.returnValue = 'Write something'
                alert("wait")
            })
        },

        methods: {
            variableInfo : function(i) {
                this.indexV = i
                this.overlayV = true
            },
            answerInfo: function(i) {
                this.indexA = i
                this.overlayA = true
            },
            previewTask : function () {
                this.taskOverlay = true

                localStorage.setItem('data', this.text)
            },
            saveText: function() {
                localStorage.setItem('data', this.text)
            },
            restoreText: function() {
                this.text = localStorage.getItem('data')
            },
            parseText : function () {

                // Tokenize
                console.log( tokenize( this.text ) )

                // parse text
                var parsedData = parseMathMarkup( this.text )

                // print out the tokenized string from the textarea
                console.log( parsedData );

                // clear arrays
                this.variables = []
                this.questions = []
                this.answers = []
                this.task_text = ""

                this.task_text = parsedData.get('task-text') // save task text
                console.log("TADAAAA",this.task_text)

                // save variables and questions
                for (let [_, value] of parsedData.entries() ) 
                {
                    console.log(_)
                    if ( value instanceof variableInfo ) this.variables.push(value)
                    if ( value instanceof taskQuestion ) this.questions.push(value)
                }

                // extract answers
                for (let question of this.questions) 
                {
                    for (let [_, value] of question.answers.entries() )
                    {
                       console.log(_)
                       this.answers.push(value)
                    }
                }

                // -------------------------------------------------
                // For all variables, compute range if range != ""
                for ( let v of this.variables ) 
                {
                    if ( v.range.length != 0 )
                    {
                        let [start, end, step] = v.range.substring(1, v.range.length-1).split(',')
                        console.log(start, end , step)
                        
                        // Get actual value
                        v.value = randomInt(parseInt(start), parseInt(end)).toString()
                    }
                    else
                    {
                        v.value = v.definition;
                    }

                    this.task_text = this.task_text.replace(v.name, v.value)

                    // For each answer replace value
                    for (let a of this.answers)
                    {
                        // Replace in correct
                        a.correct.forEach((ans, ind) => {
                            a.correct[ind] = ans.replace(v.name, v.value)                            
                        });

                        // Replace in incorrect
                        a.incorrect.forEach((ans, ind) => {
                            a.incorrect[ind] = ans.replace(v.name, v.value);
                        });
                    }
                }

                // Get final result
                for(let a of this.answers) 
                {
                    // Evaluate expressions
                    a.correct.forEach(expr => {a.correct_values.push( evaluate(expr) ) });
                    a.incorrect.forEach(expr => { a.incorrect_values.push( evaluate(expr) ) });
                }


                console.log("VARS", this.variables)
                console.log("QSTN", this.questions)
                console.log("ANSW", this.answers)
            }     
        }
    }
</script>

<style>
    .b-g {
        background-color: var(--green-background-color);
        height: 90%;
    }
    .r-g {
        height: 90%;
    }
    .h-g {
        border: solid 2px hotpink;
    }

    [text-narrow] {
        line-height: 1.1 !important;
        font-size: 1rem;
    }

    .pointer {
        cursor: pointer !important;
    }
</style>