import Vue from "vue"
import App from "./App.vue"
import VueRouter from "vue-router"
import Navbar from "./components/Navbar.vue"
import AppCreateTaskVue from "./components/AppCreateTask.vue"
import homepageVue from "./components/homepage.vue"
import createtaskpageVue from "./components/createtaskpage.vue"
import yourtasksVue from "./components/yourtasks.vue"

// Vuetify UI Components library
import vuetify from './plugins/vuetify'
import Vuetify from "vuetify/lib"
import "vuetify/dist/vuetify.min.css"

// Allow access to components and 
Vue.use(Vuetify)
Vue.use(Navbar)
Vue.use(App)
Vue.use(AppCreateTaskVue)
Vue.use(createtaskpageVue)
Vue.use(VueRouter)
Vue.use(homepageVue)
Vue.use(yourtasksVue)

// Router instance
const router = new VueRouter({

  // Define web app routes
  routes: [
    { path: "/",  component: homepageVue },
    { path: "/yourTasks", component: yourtasksVue },
    { path: "/createTask", component: createtaskpageVue },
  ]
})

// Create new Vue app
new Vue({
  router,
  components: {
    Navbar
  },
  methods: { },
  vuetify,
  render: h => h(App)
}).$mount("#app");